$main-color-opacity: rgba(103, 121, 153, 0.5);
$main-color: rgba(103, 121, 153);

@import "fonts-connector";

*,*::before,*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
}

.container {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
}

ul,ol,li {
    list-style: none
}

a {
    text-decoration: none;
}

@import 'header';

/*main-screen*/

.main-screen {
    width: 100%;
    height: 100vh;
}

@import 'footer';

@import 'fonts';