.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 20px 0px;
    background-color: $main-color-opacity;
    transition: background-color .3s ease-in-out;

    &.sticky {
        position: sticky;
        background-color: $main-color;
    }

    &__logo {
        max-width: 193px;
        max-height: 35px;
    }

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        opacity: 100%;
    }

    &__nav{

        ul {
            display: flex;
            align-items: center;

            li {
                margin-left: 17px;

                a {
                    color: #fff;
                    text-transform: uppercase;
                    transition: color .2s linear;
                    &:hover {
                        color: $main-color;
                    }
                }

                &:first-child {
                    margin-left: 0;
                }

            }

        }
        
    }
}